<template>
<div class="container">
  <div id="app">
    <AppAlert v-bind:alerts=errors />
    <router-view/>
  </div>
  </div>
</template>

<script>
import AppAlert from '@/components/AppAlert';

export default {
  name: 'App',
  components: {
    AppAlert,
  },
  data() {
    return {
      msg: 'XSAFE',
      errors: [],
    };
  },
  mounted() {
    this.$bus.$on('alert', (msg) => {
      this.errors.push(msg);
    });
  },
  watch: {
    errors() {
      setTimeout(() => {
        if (this.errors.length > 0) this.errors = this.errors.splice(1);
      }, 8000);
    },
  },
};
</script>

<style scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



