
<template>
  <div class="alert-custom">
    <div
      class="alert"
      :class="'alert-' + val.type"
      v-for="val in alerts">
      {{ val.text }}
    </div>
  </div>
</template>
<script>

export default {
  name: 'AppAlert',
  props: {
    alerts: {
      type: Array,
      required: true,
    },
  },
};
</script>
<style scoped>
.alert-custom {
    position: fixed;
    top: 0%;
    left: 5%;
    z-index:1031;
}
</style>



