<template>
  <div class="container">
    <nav class="navbar navbar-expand-md navbar-dark bg-dark mb-4 fixed-top">
      <div
        class="btn-group btn-group-toggle"
        data-toggle="buttons">
        <button
          class="btn btn-secondary"
          @click="wtype = !wtype">
          <span v-if="!wtype">Brain account</span>
          <span v-else>File account</span>
        </button>
      </div>
      <button
        class="navbar-toggler"
        type="button"
        @click="isActive = !isActive">
        <span class="navbar-toggler-icon"/>
      </button>
      <div
        :class="{ show: isActive}"
        class="navbar-collapse collapse justify-content-stretch">
        <ul class="navbar-nav ml-auto">
          <li class="nav-item">
            <button
              type="button"
              class="btn btn-secondary btn-small mr-4"
              @click="showNodeModal">Node</button>
          </li>
          <li class="nav-item">
            <span class="navbar-brand">Network:</span>
          </li>
          <li class="nav-item">
            <select
              id="network"
              class="form-control"
              v-model="form.network">
              <option selected="">mainnet</option>
              <option>testnet</option>
            </select>
          </li>
        </ul>
      </div>
    </nav>
    <div v-show="wtype">
      <h1>Each step is necessary to increase entropy</h1>
      <h4 class="alert alert-ddark">
        IMPORTANT: you need remember each step, otherwise you'll lost access to account
      </h4>
      <div class="form-row mb-3 mr-0">
        <div class="input-group-prepend col-md-12 col-xl-3">
          <span class="form-control input-group-text w-100 mb-md-2 mb-xl-0">
            1. Choose encrypt methods:
          </span>
        </div>
        <div
          class="col-md-12 col-xl-2"
          :key=i
          v-for="(value, i) in form.hash">
          <select
            id="hashfunc1"
            class="form-control"
            v-model="form.hash[i]">
            <option>md5</option>
            <option>base64</option>
            <option>hex</option>
            <option>revers</option>
            <option>sha256</option>
          </select>
        </div>
      </div>
      <div class="form-row mb-3 mr-0">
        <div class="input-group-prepend col-md-12 col-lg">
          <span class="input-group-text w-100 mb-md-2 mb-xl-0">
            2. Select number of iteration:
          </span>
        </div>
        <div class="col-md-12 col-xl-9">
          <input
            v-model.number="form.count"
            type="number"
            class="form-control"
            placeholder="Insert iteration count"
            aria-label="1-99999...">
        </div>
      </div>
      <div class="form-row mb-3 mr-0">
        <div class="input-group-prepend col-md-12 col-lg">
          <span class="input-group-text w-100 mb-md-2 mb-xl-0" >3. Select delimiter:</span>
        </div>
        <div class="col-md-12 col-xl-9">
          <select
            class="form-control"
            v-model="form.delim">
            <option
              v-for="(value, key) in delimlist"
              :value="value"
              :key="key">
              {{ value }}
            </option>
          </select>
        </div>
      </div>
      <div class="form-row mb-3 mr-0">
        <div class="col-md-12 col-xl-1">
          <button
            class="btn btn-dark"
            @click="form.phrase.push('')">+</button>
          <button
            class="btn btn-secondary"
            @click="form.phrase.pop('')">-</button>
        </div>
        <div class="input-group-prepend mr-1 col-md-* col-xl-2">
          <span
            class="input-group-text w-100 mb-md-2 mb-xl-0"
            style="height: max-content">4. Enter Passphrase:</span>
        </div>
        <div class="col-md-12 col-xl-9 row">
          <div
            v-for="(word, index) in form.phrase"
            :key="index"
            class="col-md-12 col-xl-3 mx-auto pb-2" >
            <input
              v-model.trim="form.phrase[index]"
              type="text"
              class="form-control col-10 float-left"
              :placeholder="'word '+index"
              aria-label="word index">
            <span
              v-if="index !== form.phrase.length - 1"
              class="pt-2 float-right">{{ form.delim }}</span>
          </div>
        </div>
      </div>
    </div>
    <dropZone v-show="!wtype" />
    <div class="form-row mb-3 mr-0">
      <div class="input-group-prepend col-md-12 col-xl-1">
        <span class="input-group-text w-100 mb-md-2 mb-xl-0">Address:</span>
      </div>
      <div class="col-md-12 col-xl-11">
        <input
          :value="ad"
          type="text"
          class="form-control"
          placeholder="NEM address"
          aria-label="NEM address"
          readonly>
      </div>
    </div>
    <div class="form-row mb-3 mr-0">
      <div class="input-group-prepend col-md-12 col-xl-1">
        <span class="input-group-text w-100 mb-md-2 mb-xl-0">Key:</span>
      </div>
      <div class="col-md-12 col-lg">
        <input
          :type="[isHideSk ? 'password' : 'text']"
          @click="isHideSk = !isHideSk"
          :value="sk"
          class="form-control c-pointer"
          placeholder=""
          aria-label="Secret Key"
          readonly>
      </div>
    </div>
    <button
      v-show="wtype"
      @click="genAdd"
      :disabled="btngenerate.disabled"
      class="btn btn-dark btn-lg btn-block">{{ btngenerate.value }}</button>
    <component :is="componentName" />
    <nodeHandler
      v-show="isNodeModalVisible"
      @close="closeNodeModal"
      :network="form.network" />
  </div>
</template>
<script>
/* eslint-disable import/no-unresolved */
import accountData from '@/modules/crypto';
import nextButton from '@/components/LoginNextButton';
import nodeHandler from '@/components/LoginNodeHandler';
import dropZone from '@/components/LoginDropZone';
import { getRandomInt } from '@/modules/utils';

const account = accountData.getInst();
export default {
  components: {
    nextButton,
    nodeHandler,
    dropZone,
  },
  name: 'Login',
  methods: {
    genAdd() {
      account.set(this.form);
      this.submitte();
      account.iterator()
        .then(() => {
          this.sk = account.getsk();
          this.pk = account.getpk();
          this.ad = account.getad();
          this.componentName = 'nextButton';
          this.submitte();
        });
    },
    genAddSk(h) {
      account.setsk(h).then(() => {
        this.sk = account.getsk();
        this.pk = account.getpk();
        this.ad = account.getad();
        this.componentName = 'nextButton';
      });
    },
    showNodeModal() {
      this.isNodeModalVisible = true;
    },
    closeNodeModal() {
      this.isNodeModalVisible = false;
    },
    submitte() {
      this.btngenerate.disabled = !this.btngenerate.disabled;
      this.btngenerate.value = this.btngenerate.value === 'GENERATE' ? 'WAITING...' : 'GENERATE';
    },
  },
  data() {
    return {
      submitted: false,
      btngenerate: {
        disabled: false,
        value: 'GENERATE',
      },
      isNodeModalVisible: false,
      isActive: false,
      componentName: null,
      isHideSk: true,
      ad: '',
      sk: '',
      pk: '',
      form: {
        phrase: [null, null, null, null],
        count: 2018,
        delim: '-',
        network: 'mainnet',
        hash: [
          'md5',
          'base64',
          'revers',
          'sha256',
        ],
      },
      wtype: true,
      delimlist: ['@', '#', '$', '%', '^', '*', '-', '+', 'X'],
    };
  },
  mounted() {
    this.$bus.$on('skey', (sk) => {
      this.genAddSk({
        sk,
        network: this.form.network,
      });
    });
    this.form.hash.sort(() => Math.random() - 0.5);
    this.delimlist.sort(() => Math.random() - 0.5);
    // eslint-disable-next-line prefer-destructuring
    this.form.delim = this.delimlist[0];
    this.form.count = getRandomInt(1000, 9999);
  },
};
</script>



