import Vue from 'vue';
import App from '@/App';
import router from '@/router';
import accountData from '@/modules/crypto';
import VueBus from '@/eventBus/';

Vue.use(VueBus);
const account = accountData.getInst();
router.afterEach((to) => {
  if (to.path === '/dashboard' && !account.getstate()) router.back('/');
});
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App,
  },
  template: '<App/>',
  data() {
    return {};
  },
});



