<style scoped>
  .bd-modal {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1032;
    display: block;
  }
  .modal {
    display: block;
  }
  .list-group li {
    list-style: none;
  }
</style>
<template>
  <div class="bd-modal">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Nodelist</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span @click="close" aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <form>
            <div class="form-group">
              <textarea
                class="form-control"
                v-model="text" cols="30"
                rows="10"
                id="message-text"></textarea>
            </div>
          </form>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            @click="close"
            data-dismiss="modal">Close</button>
          <button
            type="button"
            class="btn btn-dark"
            @click="pushNodes"
            :disabled="isDisabled">Add</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import nem from 'nem-sdk';
import HandlerNodes from '@/modules/handlers/nodes';
import loopArray from '@/modules/utils/loopArray';

const handlerNodes = new HandlerNodes();

export default {
  name: 'NodeHandler',
  props: {
    network: {
      type: String,
      default: 'mainnet',
    },
  },
  data() {
    return {
      errors: [],
      text: '',
      isDisabled: false,
    };
  },
  methods: {
    async getNodes() {
      if (this.network) {
        await loopArray(nem.model.nodes[this.network], handlerNodes, 'list');
        this.text = handlerNodes.list;
        handlerNodes.flushList();
      }
    },
    pushNodes() {
      if (this.errors.length === 0) {
        this.submitte();
        let nodes = this.text.split('\n');
        const reg = new RegExp(`^(?:(?:https?)://|w{3}.)(?:([-A-Z0-9+&@#/%=~_|$?!:,.]*)|
                              [-A-Z0-9+&@#/%=~_|$?!:,.])*(?:([-A-Z0-9+&@#/%=~_|$?!:,.]*)|
                              [A-Z0-9+&@#/%=~_|$])`, 'i');
        nodes = nodes.filter(n => reg.test(n));

        if (nodes.length > 0) {
          nem.model.nodes[this.network] = [];
          nodes.forEach(node => nem.model.nodes[this.network].push({ uri: node }));
        }
      }
      this.getNodes();
      this.submitte();
      this.close();
    },
    submitte() {
      this.isDisabled = !this.isDisabled;
    },
    close() {
      this.errors = [];
      this.$emit('close');
    },
  },
  mounted() {
    this.getNodes();
  },
  watch: {
    network() {
      this.getNodes();
    },
  },
};
</script>



