<template>
<router-link
    class="btn btn-secondary btn-lg btn-block"
    :to="route"
    role="button">NEXT</router-link>
</template>
<script>
export default {
  name: 'NextButton',
  data() {
    return {
      route: 'dashboard',
    };
  },
};
</script>



