
<template>
  <div class="container">
    <nav class="navbar navbar-expand-md navbar-dark bg-dark mb-4 fixed-top">
      <button
        class="navbar-toggler"
        type="button"
        @click="isActive=!isActive">
        <span class="navbar-toggler-icon"/>
      </button>
      <div
        class="navbar-collapse collapse justify-content-stretch"
        :class="{show: isActive}">
        <ul class="navbar-nav ml-auto text-white">
          <li class="nav-item">
            <strong>Address:</strong>
            <span class="mr-2">{{ address }}</span>
          </li>
          <li class="nav-item">
            <strong>Balance:</strong>
            <span class="mr-2">{{ balance }} XEM</span>
          </li>
          <li class="nav-item">
            <strong>Network:</strong>
            <span class="mr-2">{{ network }}</span>
          </li>
        </ul>
      </div>
    </nav>
    <logoutButton/>
    <button
      type="button"
      class="btn btn-dark btn-samll fixed-right"
      @click="showModal('add')">+</button>
    <modal
      v-if="isModalVisible"
      @close="closeModal"
      :type="modaltype"
      :value = "modaltype === 'decode'? this.transactions[id] : null" />
    <transactionsList
      :transactions=transactions />
  </div>
</template>
<script>
/* eslint-disable no-restricted-syntax */
import accountData from '@/modules/crypto';
import modal from '@/components/DashBoardModal';
import transactionsList from '@/components/TransactionsList';
import logoutButton from '@/components/LogoutButton';
import Account from '@/modules/account';
import HandlerTransaction from '@/modules/handlers/transaction';
import decryptpayload from '@/modules/decryptpayload';

export default {
  name: 'DashBoard',
  components: {
    modal,
    transactionsList,
    logoutButton,
  },
  data() {
    return {
      isActive: false,
      bottom: false,
      scrolling: {
        active: true,
        lastTx: '',
      },
      ready: false,
      isModalVisible: false,
      modaltype: '',
      address: '',
      publick: '',
      balance: 0,
      network: '',
      id: '',
      transactions: [],
    };
  },
  async created() {
    this.account = await accountData.getInst();
    this.address = await this.account.getad();
    this.network = await this.account.getnet();
    this.publick = await this.account.getpk();
    this.accountInf = new Account();
    this.handlerTransaction = '';

    try {
      this.accountInf.network = this.network;
      this.balance = await this.accountInf.getBalance(this.address);
      this.accountInf.api.on(`account-balance-${this.address}`, (balance) => { this.balance = balance; });
      this.accountInf.BalanceSocket(this.address, this.address);
    } catch (err) {
      this.$bus.$emit('alert', { text: `${err.message}`, type: 'danger' });
    }

    const filters = [{
      // eslint-disable-next-line consistent-return
      match({ transaction }) {
        try {
          if (transaction.recipient === this.address
              && transaction.type === 257
              && transaction.message.type === 2
              && transaction.signer === this.publick
          ) {
            return true;
          }
        } catch (e) {
          return false;
        }
      },
      address: this.address,
      publick: this.publick,
      destination: 'inctrans',
    }];

    try {
      this.handlerTransaction = new HandlerTransaction(this.address, filters, this.network);
      for (const followtag of filters) {
        this.handlerTransaction.on(followtag.destination, async (res) => {
          this.scrolling.lastTx = res.lasttrans ? res.lasttrans : this.scrolling.lastTx;
          this.scrolling.active = !res.end;
          this.addTransaction(res.list[followtag.destination], 'push');
        });
      }
      this.handlerTransaction.Start();

      const connectors = await this.handlerTransaction.ListenNewTrans('transactions');
      this.$bus.$on('socketclose', () => {
        connectors.map(connector => connector.close());
      });


      this.handlerTransaction.on('ListenNewTrans-transactions', async (data) => {
        for (const elem of data.transactions[0]) {
          this.addTransaction(elem, 'unshift');
        }
      });
    } catch (err) {
      this.$bus.$emit('alert', { text: `${err.message}`, type: 'danger' });
    }
    this.$on('scroll', function scroll() {
      try {
        this.handlerTransaction.Start(this.scrolling.lastTx);
      } catch (err) {
        this.$bus.$emit('alert', { text: `${err.message}`, type: 'danger' });
      }
    });
  },
  methods: {
    async addTransaction(obj, method = 'push') {
      const results = [];
      for (const item of obj) {
        results.push(decryptpayload(item, this.account.getsk()));
      }
      const Obj = await Promise.all(results);
      for (const decryptedPayload of Obj) {
        if (!this.inArray(this.transactions, decryptedPayload)) {
          // eslint-disable-next-line no-unused-expressions
          method === 'push'
            ? this.transactions.push(decryptedPayload)
            : this.transactions.unshift(decryptedPayload);
        }
      }
    },
    showModal(type) {
      this.modaltype = type;
      this.isModalVisible = true;
    },
    closeModal() {
      this.isModalVisible = false;
    },
    inArray(haystack, needle) {
      // eslint-disable-next-line no-restricted-syntax
      for (const h of haystack) {
        if (h.id === needle.id) return true;
      }
      return false;
    },
    bottomVisible() {
      const { scrollY } = window;
      const visible = document.documentElement.clientHeight;
      const pageHeight = document.body.offsetHeight;
      const bottomOfPage = visible + scrollY >= pageHeight;
      return bottomOfPage || pageHeight < visible;
    },
    scroll($this) {
      window.onscroll = function bottom() {
        // eslint-disable-next-line no-param-reassign
        $this.bottom = $this.bottomVisible();
      };
    },
  },
  mounted() {
    this.$bus.$on('decode', (id) => {
      this.modaltype = 'decode';
      this.isModalVisible = true;
      this.id = id;
    });
    this.scroll(this);
  },
  beforeDestroy() {
    this.$bus.$emit('socketclose', 'close');
  },
  watch: {
    bottom() {
      if (this.bottom && this.scrolling.active) {
        this.$emit('scroll', true);
      }
    },
  },
};
</script>
<style scoped>
@media (max-width: 576px) {
    .nav-item {
      font-size: 12px;
      }
}
.fixed-right {
  position: fixed;
  right: 0;
  z-index:1031;
}
</style>



