/* eslint-disable quotes */
import nem from 'nem-sdk';
import accountData from '@/modules/crypto';
import API from '@/modules//api';
import CustomError from '@/modules/handlers/customError';

const api = new API();
/**
* @name TransferTransaction
* @description preparing and sending transaction
*/

let instance;
export default class TransferTransaction {
  /**
   * @param {Object} common default common object
   * @param {Object} account
   * @param {Integer} due transaction lifetime (optional)
   */
  constructor() {
    this.common = nem.model.objects.get('common');
    this.account = accountData.getInst();
    this.network = {};
    this.due = null;
  }

  static create() {
    if (!instance) instance = new TransferTransaction();
    return instance;
  }

  prepare(type, data) {
    this.network.network = this.account.getnet();
    this.network.id = this.network.network === 'testnet' ? -104 : 104;
    switch (type) {
      case 'transferTransaction':
        return this.transferTransaction(data);
      default:
        return null;
    }
  }

  async transferTransaction(data) {
    if (!data) throw new CustomError({ code: 77, message: 'Transaction data isn\'t valid or not found' });
    const transferTransaction = nem.model.objects.get('transferTransaction');
    this.common.privateKey = this.account.getsk();

    if (!nem.utils.helpers.isPrivateKeyValid(this.common.privateKey)) {
      throw new CustomError({ code: 80, message: `Sender privatkey isn't valid` });
    }

    transferTransaction.recipient = nem.model.address.clean(this.account.getad());

    if (!nem.model.address.isValid(transferTransaction.recipient)) {
      throw new CustomError({ code: 79, message: `Recipient address isn't valid` });
    }
    transferTransaction.message = data;
    transferTransaction.amount = 0;
    transferTransaction.messageType = 2;
    transferTransaction.recipientPublicKey = this.account.getpk();
    if (
      (transferTransaction.messageType !== 2) &&
        (!nem.utils.helpers.isPublicKeyValid(transferTransaction.recipientPublicKey) ||
        nem.model.address
          .toAddress(transferTransaction.recipientPublicKey, this.network.network) ===
        transferTransaction.recipient)
    ) {
      throw new CustomError({
        code: 78,
        message: 'Recipient publickey not found or public key and address arn\'t comparable',
      });
    }

    return nem.model.transactions
      .prepare('transferTransaction')(this.common, transferTransaction, this.network.id);
  }

  async Fee(data, type = 'transferTransaction') {
    const transaction = await this.prepare(type, data);
    if (typeof (transaction) !== 'object') {
      throw new CustomError({ code: 76, message: 'fee error' });
    }
    return Promise.resolve(
      Number(
        `${nem.utils.format.nemValue(transaction.fee)[0]}.${nem.utils.format.nemValue(transaction.fee)[1]}`,
      ),
    );
  }

  /**
   * @param {Object} data
   * @param {String} type of transaction
   * @param {Integer} due lifetime of transaction
   * @returns {Promise}
 */

  async Send(data, type = 'transferTransaction') {
    api.network = this.network.network;
    const transactionEntity = await this.prepare(type, data);
    return api.send(this.common, transactionEntity, this.due);
  }
}



