<style scoped>
.blur-text {
  overflow: hidden;
  text-overflow: ellipsis;
  filter: blur(2.1px);
}
</style>
<template>
<ol class="list-group">
<li class="list-group-item" :key=index v-for="(value, index) in transactions">
    <div class="row">
      <div class="col-lg-2">{{ value.timestamp }}</div>
      <div class="col-lg-4">{{ value.body.meta }}</div>
      <div class="col-lg-4 blur-text">{{ value.body.text }}</div>
      <div class="col-lg-2">
        <button
            @click="decode(index)"
            class="btn btn-dark btn-small">
            decrypt</button>
      </div>
    </div>
</li>
    </ol>
</template>
<script>

export default {
  name: 'TransactionsList',
  props: {
    transactions: {
      type: Array,
      required: true,
    },
  },
  methods: {
    decode(id) {
      this.$bus.$emit('decode', id);
    },
  },
};
</script>



