import nem from 'nem-sdk';
import { ConverGMTdata } from './utils';

export default async function decryptpayload({ meta, transaction }, secretkey) {
  const dectrans = {
    id: meta.hash.data,
    timestamp: ConverGMTdata(nem.utils.format.nemDate(transaction.timeStamp)),
    body: '',
  };
  const text = await nem.crypto.helpers
    .decode(secretkey, transaction.signer, transaction.message.payload);
  const textUtf8 = nem.utils.convert.utf82rstr(nem.utils.convert.hex2a(text));
  const result = textUtf8.match(/^(.+)%(.*)$/);
  dectrans.body = result
    ? { meta: result[1], text: result[2] }
    : { meta: '', text: textUtf8.replace('%', '') };
  return dectrans;
}



