/* eslint-disable no-ex-assign */
/* eslint-disable consistent-return */
/* eslint-disable no-restricted-syntax */
/* eslint-disable class-methods-use-this */
/* eslint-disable no-param-reassign */
/* eslint-disable array-callback-return */
import EventEmitter from 'events';
import nem from 'nem-sdk';
import API from '../api';
import CustomError from './customError';

class HandlerTransaction extends EventEmitter {
  /**
       * @constructor FilteringTransactions
       * @param {String} address - account address
       * @param {Object} filters - filters for filtering transactions
  */
  constructor(address, filters, network) {
    super();
    this.Address = address;
    this.api = new API();
    this.api.network = network;
    this.countWebsocket = 5;
    /**
       * @property {Array} [list] - filtered trans
       * @property {String} [lasttrans] - hash last trans
     */
    this.PreparePost = {
      list: [],
      lasttrans: '',
      end: false,
    };
    this.filters = filters;
    this.Filters = filters;
    this.FilterSocket = filters;
    this.Filters.forEach((elem) => {
      if (!this.PreparePost.list[elem.destination]) this.PreparePost.list[elem.destination] = [];
    });
  }
  /**
       * @description filtering trans
       * @param {Object} data - list of transactions
       * @param {Array} rules - set of filters
       * @returns {Object} res
       */
  split(data, rules) {
    const res = [];
    for (const item of data) {
      for (const rule of rules) {
        if (rule.match(item)) {
          if (!res[rule.destination]) res[rule.destination] = [];
          res[rule.destination][res[rule.destination].length] = item;
        }
      }
    }
    return res;
  }

  /**
    * @description clearing
    * @deprecated
  */
  flushPreparePost() {
    this.PreparePost.list = [];
    this.PreparePost.lasttrans = '';
  }

  /**
    * @description
    * @param {String} txHash
  */
  async Start(txHash = '') {
    // loop filters
    this.Filters.forEach((elem) => {
      if (
        this.PreparePost.list[elem.destination].length >= 25 ||
        this.PreparePost.end
        // Object.prototype.hasOwnProperty.call(this.PreparePost, 'err')
      ) {
        this.emit(elem.destination, this.PreparePost);
        this.PreparePost.list[elem.destination] = [];
        delete this.PreparePost.err;
        this.Filters = this.Filters
          .filter(elemfl => elemfl.destination !== elem.destination);
      }
    });

    if (!this.Filters.length) {
      this.flushPreparePost();
      this.Filters = this.filters;
      // think about it later ....
      this.FilterSocket.forEach((e) => {
        if (!this.PreparePost.list[e.destination]) this.PreparePost.list[e.destination] = [];
      });
      return true;
    }

    try {
      const checkedTrans = await this.checkIncTrans(txHash, this.Filters);
      this.PreparePost.lasttrans = checkedTrans.lasttrans;
      for (const elem of this.Filters) {
        if (checkedTrans.transactions[0][elem.destination]) {
          this.PreparePost.list[elem.destination] =
              this.PreparePost.list[elem.destination]
                .concat(checkedTrans.transactions[0][elem.destination]);
        }
      }

      this.Start(this.PreparePost.lasttrans);
    } catch (err) {
      if (err.code === 98) {
        this.PreparePost.end = true;
        err = null;
      } else {
        this.PreparePost.end = false;
      }
      this.PreparePost.err = err;
      this.FilterSocket.forEach(async (elem) => {
        await this.emit(elem.destination, this.PreparePost);
        // this.PreparePost.list[elem.destination] = await []
      });
    }
  }

  /**
    * @description valid trans
    * @param {String} txHash - trans hash
    * @returns {Object} PrepResData
  */
  async checkIncTrans(txHash, filters) {
    if (!nem.model.address.isValid(this.Address)) throw new CustomError({ code: 55, message: 'address isn\'t defined' });
    const PrepResData = {
      transactions: [],
    };
    const result = await this.api.transactionIncoming(this.Address, txHash);
    if (!result.data.length) throw new CustomError({ code: 98, message: 'transaction\'s pool is empty' });
    PrepResData.lasttrans = result.data[result.data.length - 1].meta.hash.data;
    PrepResData.transactions.push(this.split(result.data, filters));
    return Promise.resolve(PrepResData);
  }

  async ListenNewTrans(followEvnt) {
    if (!nem.model.address.isValid(this.Address)) throw new CustomError({ code: 55, message: 'address isn\'t defined' });
    const connectors = await this.api.createConnector(this.Address, this.countWebsocket);
    connectors.map((connector) => {
      connector.listener = `${this.Address}-${followEvnt}`;
      this.api.subscribeTransactionConfirmed(connector);
    });

    this.api.on(`transaction-confirmed-${this.Address}-${followEvnt}`, async (res) => {
      const data = {
        transactions: [],
      };
      data.transactions.push(this.split([res], this.FilterSocket));
      this.emit(`ListenNewTrans-${followEvnt}`, data);
    });
    return connectors;
  }
}

export { HandlerTransaction as default };



