export default class HandlerNodes {
  constructor() {
    this.$nodes = [];
    this.$list = '';
  }

  set nodes(val) {
    this.$nodes.push({ uri: val });
  }

  get nodes() {
    return this.$nodes;
  }

  set list(val) {
    this.$list += `${val.uri}\n`;
  }

  get list() {
    return this.$list;
  }

  flushList() {
    this.$list = '';
  }

  flushNodes() {
    this.$nodes = [];
  }
}



