<template>
<div>
  <div class="form-group" ref="fileform"
           @dragstart="emitEvent"
           @dragend="emitEvent"
           @dragenter.prevent.stop="emitEvent"
           @dragover.prevent.stop="emitEvent"
           @drop.prevent.stop="emitEvent"
           @click="inputEl">
   <div class="dropzone">
    <h1>Drop files here or click for select</h1>
    <h4>You can use any file</h4>
    <h4>Derived sha256sum from file uses as private key</h4>
    <h4 class="alert alert-ddark">
      if you lost or modified the file, you'll never get access to you account
    </h4>
    <input type="file" ref="fileinput" @change="emitEvent" id="file">
   </div>
  </div>
</div>
</template>
<script>
export default {
  name: 'DropZone',
  mounted() {
    this.$on('hashfile', (e) => {
      this.hashfile(e);
    });
  },
  methods: {
    emitEvent(event) {
      if (event.type === 'drop') this.$emit('hashfile', event.dataTransfer.files[0]);
      if (event.type === 'change') this.$emit('hashfile', event.target.files[0]);
    },
    inputEl() {
      this.$refs.fileinput.click();
    },
    hashfile(file) {
      const reader = new FileReader();
      reader.readAsArrayBuffer(file);
      reader.onload = () => {
        window.crypto.subtle.digest('SHA-256', reader.result)
          .then((hash) => {
            let hexString = '';
            const hashResult = new Uint8Array(hash);
            for (let i = 0; i < hashResult.length; i += 1) {
              hexString += (`00${hashResult[i].toString(16)}`).slice(-2);
            }
            this.$bus.$emit('skey', hexString);
          });
      };
    },
  },
};
</script>

<style scoped>
#file {
 visibility: hidden
}
.dropzone {
    color: black;
    border-color: #000;
    border-style: dashed;
    border-width: 8px;
}
</style>



