/* eslint-disable consistent-return */
import deffer from './deffer';

export default function loopArray(obj, func, method) {
  const deffered = deffer();
  if (typeof (obj) !== 'object') return deffered.reject('type is not object');
  const { length } = obj;
  let index = 0;
  const Func = func;
  const out = '';
  const loop = () => {
    if (index < length) {
      try {
        Func[method] = obj[index];
      } catch (e) {
        console.error(e);
      }
      index += 1;
      setTimeout(loop(), 0);
    } else {
      return deffered.resolve(out);
    }
  };
  loop();
  return deffered.promise;
}



