<template>
  <div class="bd-modal position-fixed">
    <div
      class="modal-dialog"
      role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5
            class="modal-title"
            id="exampleModalLabel">{{ type }}</h5>
          <button
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close">
            <span
              @click="close"
              aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <form v-if="this.type === 'addnodes'">
            <div class="form-group">
              <div
                for="meta"
                class="col-form-label float-left">Nodelist:</div>
              <textarea
                class="form-control"
                v-model="text"
                :maxlength="textlimit"
                cols="30"
                rows="10"
                id="message-text"/>
            </div>
            <div class="input-group mb-3">
              <input
                type="text"
                class="form-control"
                placeholder="add ip or domain"
                aria-label="Recipient's username"
                aria-describedby="basic-addon2">
              <div class="input-group-append">
                <button
                  type="button"
                  class="btn btn-dark"
                  @click="addnode"
                  :disabled="submitted">+</button>
              </div>
            </div>
          </form>
          <form v-if="this.type !== 'addnodes'">
            <div class="form-group">
              <div
                for="meta"
                class="col-form-label float-left">Meta:</div>
              <div
                class="float-right">{{ metaByte }} CharByte</div>
              <input
                type="text"
                :maxlength="metalimit"
                v-model="meta"
                id="meta"
                class="form-control col-11">
            </div>
            <div class="form-group">
              <div
                for="message-text"
                class="col-form-label float-left">Note:</div>
              <div class="float-right">{{ textByte }} CharByte</div>
              <textarea
                class="form-control"
                v-model="text"
                :maxlength="textlimit"
                cols="30"
                rows="10"
                id="message-text" />
            </div>
            <div class="form-group">
              <p
                class="alert alert-danger list-group"
                v-if="errors.length">
                <b>Please correct the following error(s):</b>
                <ul class="list-group">
                  <li
                    :key="error"
                    v-for="error in errors">{{ error }}</li>
                </ul>
              </p>
              <label
                for="password"
                class="col-form-label float-left">Password:</label>
              <input
                type="password"
                class="form-control"
                id="password"
                v-model="pass">
              <label
                for="password"
                class="col-form-label float-left"
                v-if="this.type === 'add'">Confirm Password:</label>
              <input
                type="password"
                class="form-control"
                id="password1"
                v-if="this.type === 'add'"
                v-model="pass2"
                @change="comparePass">
              <label
                for="fee"
                class="col-form-label float-left">Fee: {{ fee }}</label>
            </div>
          </form>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            @click="close"
            data-dismiss="modal">Close</button>
          <button
            type="button"
            v-show="this.type === 'add'"
            class="btn btn-dark"
            @click="send"
            :disabled="submitted">Send</button>
          <button
            type="button"
            v-show="this.type === 'decode'"
            class="btn btn-dark"
            @click="decode"
            :disabled="submitted">Decode</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable import/no-unresolved */
import ciphertext from '@/modules/ciphertext';
import TransferTransaction from '@/modules/transfer.transaction';

const transfer = new TransferTransaction();
const Str2Buffer = new TextEncoder('utf-8');
export default {
  name: 'Modal',
  props: {
    type: {
      type: String,
      default: '',
    },
    value: {
      type: Object,
      required: true,
    },
  },
  methods: {
    close() {
      this.pass = '';
      this.pass2 = '';
      this.errors = [];
      this.submitted = false;
      this.$emit('close');
    },
    pushError(err) {
      if (!this.errors.includes(err)) this.errors.push(err);
    },
    errorflush() {
      this.errors = [];
    },
    comparePass() {
      this.errorflush();
      if (this.pass !== this.pass2) this.pushError('The password isn\'t confirm');
    },
    async send() {
      this.errorflush();
      this.comparePass();
      if (!this.text) this.pushError('The text is empty');
      if (!this.pass) this.pushError('The password is empty');
      if (!this.errors.length) {
        try {
          this.isSubmitted();
          const crypt = ciphertext.encrypt(this.pass, this.text);

          const data = this.meta ? `${this.meta}%${crypt}` : crypt;
          const result = await transfer.Send(data);
          this.$bus.$emit('alert', { text: `tx: ${result.transactionHash.data}`, type: 'success' });
          this.isSubmitted();
          this.close();
        } catch (err) {
          this.$bus.$emit('alert', { text: `${err.message}`, type: 'danger' });
          this.isSubmitted();
          this.pushError(err.message);
        }
      }
    },
    decode() {
      const decrypted = ciphertext.decrypt(this.pass, this.text);
      if (decrypted) {
        this.text = decrypted;
      } else {
        this.pushError('The password is wrong');
      }
    },
    isSubmitted() {
      this.submitted = !this.submitted;
    },
  },
  data() {
    return {
      errors: [],
      textlimit: 701,
      metalimit: 26,
      text: '',
      meta: '',
      pass: '',
      pass2: '',
      fee: 0,
      submitted: false,
      textByte: 0,
      metaByte: 0,
    };
  },
  mounted() {
    this.metaByte = this.metalimit;
    this.textByte = this.textlimit;
    if (this.type === 'decode') {
      this.text = this.value.body.text;
      this.meta = this.value.body.meta;
    }
  },
  destroyed() {
    this.text = '';
  },
  computed: {
    watchtxt() {
      return this.text + this.meta;
    },
    watchpass() {
      return this.pass && this.pass2;
    },
  },
  watch: {
    type() {
      if (this.type === 'add') {
        this.text = '';
        this.meta = '';
      }
    },
    watchpassass() {
      if (this.type === 'add') this.comparePass();
    },
    meta() {
      const metaBufferLength = Str2Buffer.encode(this.meta).length;
      this.metaByte = this.metalimit - metaBufferLength;
      if (metaBufferLength > this.metalimit) {
        this.meta = this.meta
          .substr(0, this.meta.length - ((metaBufferLength - this.metalimit) / 2));
      }
    },
    text() {
      const textBufferLength = Str2Buffer.encode(this.text).length;
      this.textByte = this.textlimit - textBufferLength;
      if (textBufferLength > this.textlimit) {
        this.text = this.text
          .substr(0, this.text.length - ((textBufferLength - this.textlimit) / 2));
      }
    },
    async watchtxt() {
      if (this.type === 'add') {
        const fullData = this.text + this.meta;
        this.fee = fullData ? await transfer.Fee(fullData) : 0;
        if (this.fee > this.$parent.balance) {
          this.submitted = true;
          this.pushError('Influent balance');
        } else {
          this.errorflush();
          this.submitted = false;
        }
      }
    },
  },
};
</script>
<style scoped>
  .bd-modal {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1032;
    display: block;
    overflow-x: scroll;
  }
  .modal {
    display: block;
  }
  .list-group li {
    list-style: none;
  }
</style>



