import CryptoJS from 'crypto-js';

const CipherText = {
  encrypt: (key, data) => {
    const iv = CryptoJS.enc.Hex.parse(
      CryptoJS.lib.WordArray.random(32 / 8).toString(),
    );
    const Key = CryptoJS.enc.Hex.parse(
      CryptoJS.SHA3(key, { outputLength: 256 }).toString(),
    );
    const options = {
      iv,
      mode: CryptoJS.mode.CBC,
    };
    const encrypted = CryptoJS.AES.encrypt(data, Key, options);
    return iv.toString(CryptoJS.enc.Base64) + encrypted.toString();
  },

  decrypt: (key, data) => {
    const Key = CryptoJS.enc.Hex.parse(
      CryptoJS.SHA3(key, { outputLength: 256 }).toString(),
    );
    const iv = CryptoJS.enc.Hex.parse(
      CryptoJS.enc.Base64.parse(data.substr(0, 8)).toString(),
    );
    const Data = data.substr(8).toString();
    const options = {
      iv,
      mode: CryptoJS.mode.CBC,
    };
    const decrypt = CryptoJS.AES.decrypt(Data, Key, options);
    try {
      const decode = decrypt.toString(CryptoJS.enc.Utf8);
      return decode.length > 0 ? decode : null;
    } catch (e) {
      return null;
    }
  },
};
Object.freeze(CipherText);
export default CipherText;



